.user-list{
    display:flex;
    flex-wrap: wrap;
    max-width:1200px;
    margin:auto;
    a{
        text-decoration: none;
    }
}




@media(max-width:1146px){
    .user-list{
        justify-content: center;
    }
}

@media(max-width:415px){
    .user-list{
        padding:0;
    }
 }